* {
  box-sizing: border-box;
}

body {
  background-image: url("si-ukr-mod.23a35f11.jpg");
  background-repeat: no-repeat;
  margin: 0;
  padding: 20px;
  font-family: Roboto Slab;
  font-size: medium;
}

.hero-title {
  text-align: center;
  color: #2c2646;
  margin-top: 90px;
  font-size: 45px;
}

input[type="text"]::-ms-input-placeholder {
  color: #2c2646;
  opacity: .7;
  font-size: medium;
  font-style: italic;
}

input[type="text"]::placeholder {
  color: #2c2646;
  opacity: .7;
  font-size: medium;
  font-style: italic;
}

.search-form {
  text-align: center;
  margin-bottom: 20px;
}

.search-container {
  flex-direction: column;
  align-items: center;
  display: flex;
}

.search-input {
  width: 300px;
  border: 1px solid #dfe1e5;
  border-radius: 24px;
  outline: none;
  padding: 10px 16px;
  font-size: 18px;
  transition: border-color .3s ease-in-out;
}

.search-input:focus {
  border-color: #4d90fe;
  box-shadow: 0 0 5px rgba(0, 0, 0, .2);
}

.search-button {
  color: #5f6368;
  cursor: pointer;
  background-color: #f2f2f2;
  border: 1px solid rgba(0, 0, 0, 0);
  border-radius: 3px;
  margin-top: 25px;
  margin-bottom: 25px;
  padding: 8px 16px;
  font-size: 15px;
  transition: background-color .3s ease-in-out;
}

.search-button:hover {
  opacity: .5;
  color: #2c2646;
  background-color: #e8e8e8;
  border: 1px solid;
  border-radius: 3px;
  box-shadow: 0 0 5px rgba(0, 0, 0, .2);
}

.gallery {
  grid-gap: 20px;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  display: grid;
}

.photo-card {
  background-color: #f4f4f4;
  border-radius: 5px;
  padding: 10px;
  box-shadow: 0 2px 4px rgba(0, 0, 0, .1);
}

.photo-card img {
  width: 100%;
  height: auto;
  border-radius: 5px;
  margin-bottom: 10px;
}

.info {
  margin-top: 10px;
}

.info-item {
  margin-bottom: 5px;
  font-weight: bold;
}

input[type="text"].search-input {
  border: 1px solid #ddd;
  border-radius: 4px;
  outline: none;
  padding: 8px 12px;
  font-size: 14px;
  transition: box-shadow .3s;
}

input[type="text"].search-input:hover {
  box-shadow: 0 0 5px rgba(0, 0, 0, .1);
}

input[type="text"].search-input:focus {
  box-shadow: 0 0 5px rgba(0, 0, 0, .2);
}

